<template>
  <!-- 商品查看详情 -->
  <div class="details-body">
    <div class="details-header">
      <a-breadcrumb separator=">">
        <a-breadcrumb-item>商品管理</a-breadcrumb-item>
        <a-breadcrumb-item
          ><a href="/productlist">商品列表</a></a-breadcrumb-item
        >
        <a-breadcrumb-item>{{ this.$route.meta.title }}</a-breadcrumb-item>
      </a-breadcrumb>
    </div>
    <hr style="color: #666" />
    <h3>{{ this.$route.meta.title }}</h3>
    <div class="details-conter">
      <div><h3 style="text-align: left">商品基本信息</h3></div>
      <div>
        <span>*商品类型：</span>
        <span>{{ detlist.classId }}</span>
      </div>
      <div>
        <span>*商品名称：</span><span>{{ detlist.goodsName }} </span>
      </div>
      <div>
        <span>*库存数：</span><span>{{ detlist.goodsNum }}</span>
      </div>
      <div>
        <span>商品简介：</span><span>{{ detlist.goodsBrief }}</span>
      </div>
      <div>
        <span>商品图片：</span>
        <img width="100" height="80" src="" alt="" />
      </div>
      <div>
        <span> 商品详情图：</span>
        <img width="100" height="80" src="" alt="" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false,
      imageUrl: "",
      detlist: [],
      list: [],
    };
  },
  mounted() {
    this.details();
    this.getpic();
    this.classlist();
  },

  methods: {
    async getpic() {
      let str = {
        merGoodsId: this.$route.query.id,
      };
      let { data } = await this.$http.piclist(str);
      if (data.code == "0000") {
        console.log(data);
      }
    },
    async details() {
      let str = {
        id: Number(this.$route.query.id),
      };
      let { data } = await this.$http.details(str);
      if (data.code == "0000") {
        console.log(data.details);
        let destr = data.details;
        for (let i in destr) {
          this.list.map((str) => {
            if (destr.classId == str.id) {
              destr.classId = str.className;
              this.detlist = destr;
            }
          });
        }
      }
    },
    async classlist() {
      //商品分类
      let { data } = await this.$http.classlist();
      this.list = [...data.list];
    },
  },
};
</script>

<style scoped>
.details-conter div {
  display: flex;
  justify-content: left;
  align-items: center;
  border: 1px solid rgb(146, 146, 146);
  border-bottom: 0;
  padding: 10px 300px;
}

.details-conter div > :nth-child(1) {
  width: 10%;
  text-align: right;
}
.details-conter > :nth-child(1) {
  height: 40px;
  padding-left: 10px;
}

.details-conter > :nth-child(7) {
  border: 1px solid rgb(146, 146, 146);
}
</style>